#root {
  margin: 0 10px;
}
body {
  font-family: "Roboto", sans-serif;
  position: relative;
  background-color: #5a5a9d;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
}
.img {
  width: inherit;
}
.enter-icon {
  width: 50px;
  z-index: -100;
  cursor: pointer;
}
.hidden {
  display: none;
}
a {
  font-size: 20px;
  color: aliceblue;
}
.form {
  width: 100%;
  min-width: 300px;
  height: inherit;
}

.modal-auth {
  width: 60%;
  height: 40%;
  min-width: 300px;
  max-width: 600px;
  max-height: 300px;
  min-height: 300px;
  margin: 0 auto;
  background-color: #dcdcff;
  position: absolute;
  top: 25%;
  left: 25%;
  border-radius: 5%;
  border: 10px solid #8a8aca;
}

h1 {
  margin: 35px auto;
  font-family: "Roboto", sans-serif;
  text-align: center;
}
.div-form {
  text-align: center;
}

.auth-button {
  display: block;
  margin: 15px auto;
