  cursor: pointer;
  font-size: 20px;
  color: #000;
}
.out-button {
  display: block;
  margin: 140px auto;
  font-size: 20px;
  text-align: center;
  width: 100px;
  height: 40px;
  border-radius: 5px;
  color: aliceblue;
  background-color: blueviolet;
}
.modal-err {
  width: 60%;
  height: 40%;
  min-width: 300px;
  max-width: 600px;
  max-height: 300px;
  min-height: 300px;
  margin: 0 auto;
  background-color: rgb(185, 61, 90);
  font: black;
  color: aliceblue;
  position: absolute;
  top: 25%;
  left: 25%;
  border-radius: 5%;
  border: 10px solid #8a8aca;
}
.error-modal-text {
  margin: 25px auto;
  text-align: center;
  font-size: 20px;
}
.error-modal-button {
  display: block;
  margin: 140px auto;
  font-size: 20px;
  text-align: center;
  width: 100px;
  height: 40px;
  border-radius: 5px;
  color: aliceblue;
  background-color: rgb(226, 43, 159);
}
.calendar-wrapper {
  width: 360px;
  margin: 3em auto;
  padding: 2em;
  border: 1px solid #dcdcff;
  border-radius: 10px;
  background: aliceblue;
}
table {
  width: 100%;
  border: 1px solid #dcdcff;
  border-radius: 3px;
  border-collapse: collapse;
  color: #444;
}
td {
  position: relative;
  height: 48px;
  text-align: center;
  vertical-align: top;
  border-right: 1px solid #dcdcff;
  border-top: 1px solid #dcdcff;
  width: 14%;
  font-weight: bold;
}

thead td {
  border: none;
  color: #28283b;
  text-transform: uppercase;
  font-size: 1.5em;
}

td.not-current {
  color: #c0c0c0;
}

td.today {
  font-weight: 700;
  color: #28283b;
  font-size: 1.5em;
}

#btnPrev {
  float: left;
  margin-bottom: 20px;
}
#btnPrev:before {
  content: "\\25C4";
  padding-right: 4px;
}
#btnNext {
  float: right;
  margin-bottom: 20px;
}
#btnNext:after {
  content: "\\25BA";
  padding-left: 4px;
}
#btnPrev,
#btnNext {
  background: transparent;
  border: none;
  outline: none;
  font-size: 1em;
  color: #c0c0c0;
  cursor: pointer;
  font-family: sans-serif;
  text-transform: uppercase;
  transition: all 0.3s ease;
}

#btnPrev:hover,
#btnNext:hover {
  color: #28283b;
  font-weight: bold;
}

#divCont {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas:
    "input input input"
    "input-date select button"
    "list list list"
    "container container container";
}

.input-todos {
  width: 80%;
  margin: 20px auto;
  text-align: center;
  grid-area: input;
  height: 30px;
  border-radius: 3%;
}
.input-date {
  grid-area: input-date;
  width: 150px;
  display: block;
  margin: 0 auto 10px;
  height: 25px;
}
.input-status {
  grid-area: select;
  width: 150px;
  display: block;
  margin: 0 auto;
  height: 25px;
}
.main-button {
  grid-area: button;
  display: block;
  width: 250px;
  height: 40px;
  border-radius: 5px;
  color: aliceblue;
  background-color: rgb(55, 70, 185);
  font-size: 20px;
}

#tasks-container {
  width: 100%;
  display: grid;
  grid-template-columns: 131px 125px 3fr 1fr 1fr 1fr;
  grid-template-rows: auto;
  grid-area: container;
  background-color: aliceblue;
  border: 3px solid black;
  border-top: none;
}
.list {
  margin-top: 20px;
  width: 100%;
  display: grid;
  grid-template-columns: 131px 125px 3fr 1fr 1fr 1fr;
  grid-template-rows: auto;
  text-align: center;
  grid-area: list;
  background-color: aliceblue;
  border: 3px solid black;
}
.title0 {
  margin: 0 auto;
  border-right: 2px dotted black;
  width: 100%;
  font-size: 16px;
  font-weight: bold;
}
.title0 > select {
  display: block;
  background-color: antiquewhite;
  margin: 0 auto;
  font-weight: 300;
}
.list-dates {
  border: 2px dotted black;
  margin: 0 auto;
  width: 100%;

  text-align: center;
}
.title1 {
  margin: 0 auto;
  border-right: 2px dotted black;
  width: 100%;
  font-size: 16px;
  font-weight: bold;
}

.list-time {
  border: 2px dotted black;
  margin: 0 auto;
  width: 100%;
  min-width: 99px;
  text-align: center;
}
.title2 {
  margin: 0 auto;
  border-right: 2px dotted black;
  width: 100%;
  min-width: 400px;
  min-width: 402px;
  font-size: 16px;
  font-weight: bold;
}
.title2 > input {
  display: block;
  background-color: antiquewhite;
  margin: 0 auto;
  width: 80%;
  font-weight: 300;
}
.taska {
  border: 2px dotted black;
  margin: 0 auto;
  width: 100%;
  text-align: center;
  min-width: 402px;
}
.title3 {
  margin: 0 auto;
  border-right: 2px dotted black;
  width: 100%;
  min-width: 95px;
  font-size: 16px;
  font-weight: bold;
}
.title3 > select {
  display: block;
  background-color: antiquewhite;
  margin: 0 auto;
  font-weight: 300;
}
.status-title {
  border: 2px dotted black;
  margin: 0 auto;
  width: 100%;
  text-align: center;
  min-width: 95px;
}
.title4,
.title5 {
  margin: 0 auto;
  border-right: 2px dotted black;
  width: 100%;
  font-size: 16px;
  font-weight: bold;
  min-width: 58px;
}

.current-edit-button,
.current-delete-button {
  min-width: 58px;
  background-color: rgb(116, 128, 222);
  border-radius: 15%;
}
.title5 {
  border: none;
}

.flex {
  background-color: #c0c0c0;
  width: 400px;
  height: 30%;
  position: absolute;
  z-index: 100;
  border-radius: 5%;
  border: 10px solid #8a8aca;
  padding-left: 40px;
}

.btn {
  background-color: black;
  width: 20%;
  position: absolute;
  top: 80%;
  color: #fff;
  z-index: 101;
}
.modal {
  width: 400px;
  height: 300px;
}
.flexy {
  width: inherit;
  height: inherit;
}
label {
  display: block;
}
tr {
  padding-top: 15px;
  vertical-align: middle;
}
